<template>
  <nav-bar :is-back="$route.meta.isBack" v-model:show="show" :title="title"></nav-bar>

  <van-popup v-model:show="show" :overlay="false" position="right" :style="{ height: '100%',width: '70%' }" >
    <div style="height: 100%" ref="target">
      <div @click="show=false" style="height: var(--van-nav-bar-height);line-height: var(--van-nav-bar-height)">
        <van-icon color="#fff" name="cross" size="18" />
      </div>
      <menu-list v-model:showLangAction="showLangAction" v-model:showPopup="show"></menu-list>
    </div>
  </van-popup>

  <lang-action v-model:show="showLangAction"></lang-action>

  <div class="content" style="margin-top: var(--van-nav-bar-height)">
    <router-view />
  </div>

  <nav-footer class="footer" :icons="icons"></nav-footer>
</template>

<script setup>
import NavBar from './components/NavBar'
import MenuList from './components/MenuList'
import LangAction from './components/LangAction'
import NavFooter from './components/NavFooter'
import i18n from './i18n-setup'

import { onClickOutside } from '@vueuse/core'

import {computed, ref} from 'vue'
import {serializationReverse} from "@/util";
import router from "@/router";
import {publicConfig} from "@/lang/public";
const show = ref(false)
const showLangAction = ref(false)

// 页脚图标配置
const icons = publicConfig.footerIcons

const target = ref(null)
onClickOutside(target, () => {
  show.value = false
})
const title = computed(() => {
  return serializationReverse(i18n.global.t('router'))[0][router.currentRoute.value.path]
})

</script>

