<template>
  <van-list

  >
    <van-cell @click="handleClickCell(item, index)" v-for="(item, index) in menu" :key="item"
              :title="handleTitle(item)" class="menu-list__cell-active"/>
    <van-cell @click="handleClickCell('language')" key="language" :title="$t('language')"
              class="menu-list__cell-active"/>
  </van-list>
</template>

<script setup>
import {computed, defineEmits, defineProps} from "vue";
import router, {menu} from "@/router";
import {i18n} from '@/i18n-setup'
import {serializationReverse} from "@/util";

const emit = defineEmits(['update:showLangAction','update:showPopup'])
const props = defineProps({
  showLangAction: {
    type: Boolean,
    default: false
  },
  showPopup: {
    type: Boolean,
    default: false
  },
})

const showLangAction = computed({
  get() {
    return props.showLangAction
  },
  set(val) {
    emit('update:showLangAction', val)
  }
})
const showPopup = computed({
  get() {
    return props.showPopup
  },
  set(val) {
    emit('update:showPopup', val)
  }
})

function handleClickCell(item, index) {
  if (item == 'language') {
    showLangAction.value = true
  } else {
    router.push(menu[index])
    showPopup.value = false
  }
}

function handleTitle(item) {
  return serializationReverse(i18n.global.t('router'))[0][item.path]
}
</script>

<style>
.menu-list__cell-active:active {
  background-color: #333;
  color: var(--van-primary-color);
}
</style>
