import {Dialog, Field} from "vant";
import 'vant/es/dialog/style';
import i18n from "@/i18n-setup";
import {ref} from "vue";
import contractInterface from "@/contractInterfaceWrap";
import {errorNotify, handleError, isAddress, loading, loadingClose, successToast} from "@/util";

export async function invitationSetup({to, from, next}) {
    const loadingInstance = loading();
    try {
        const isNeed = await fetchIsNeedShowDialog()
        if(!isNeed){
            console.log('is not need dialog...')
            return;
        }
        console.log('is need dialog...')
        const address = getAddress({to, from, next})
        let value1 = ref(address)
        const formatter = value => value
        // let confirmButtonDisabled = ref(!isAddress(address))
        Dialog.alert({
            title: i18n.global.t('bindTopLevel'),
            message: () => <Field
                v-model={value1.value}
                maxlength="42"
                label={''}
                formatter={formatter}
                placeholder={i18n.global.t('invitationAddress')}
            />,

            // 处理不是地址不能关闭弹框开始
            // confirmButtonDisabled: confirmButtonDisabled,
            'before-close': async () => {
                if (!isAddress(value1.value)) {
                    errorNotify(i18n.global.t('isNotRightInvitationAddress'))
                    return false
                }
                // confirmButtonDisabled.value = true
                try {
                    const result = await contractInterface.fetchInviteAddress(value1.value)
                    if (result) {
                        successToast(i18n.global.t('successfully'))
                    } else {
                        return false
                    }
                } catch(e) {
                    handleError(e)
                    return false
                } finally {
                    // TODO...
                }
                return true
            }
            // 处理不是地址不能关闭弹框结束
        })
    } catch(e) {
        handleError(e)
    } finally {
        loadingClose(loadingInstance);
        console.log('invitationSetup finish...')
        if (typeof next === 'function') {
            next()
        }
    }


}

// 处理是否需要弹框
function fetchIsNeedShowDialog() {
    return contractInterface.fetchIsNeedShowDialog()
}

// 获取地址
function getAddress({to}) {
    const invitation = to.query.invitation
    if (invitation) {
        return invitation
    }
}
