<template>
  <van-nav-bar
      fixed
      @click-right="handleClickRight"
      safe-area-inset-top
  >
    <template #left>
      <van-icon v-if="isBack" @click="$router.go(-1)" name="arrow-left" size="18" />
    </template>

    <template #right>
      <van-icon v-if="!show" name="wap-nav" size="18" />
      <van-icon v-else name="cross" size="18" />
    </template>

    <template #title>
      <van-row align="center" justify="center">
        <img width="30" src="../assets/logo.png" alt="" style="padding-right: 2px">
        {{title}}
      </van-row>
    </template>
  </van-nav-bar>
</template>

<script setup>
import {defineProps,defineEmits} from "vue";
const emit = defineEmits(['update:show'])

const props = defineProps({
  show: {
    type: Boolean,
    default: false
  },
  title: {
    type: String,
    default: ''
  },
  isBack: {
    type: Boolean,
    default: false
  }
})
function handleClickRight() {
  emit('update:show', !props.show)
}
</script>

<style>
.van-nav-bar__content {
  backdrop-filter: saturate(150%) contrast(50%) blur(8px);
  background: rgba(0,0,0,.5);
}
</style>
