<template>
  <div class="share" >
    <div style="height: 20px"></div>
    <home-card style="margin: 20px 16px">
      <home-title>{{ $t('share.title') }}</home-title>
      <div style="height: 20px"></div>
      <img v-if="url" class="mt-6 mb-2 rounded border" :src="qrcode" alt="QR Code">
      <home-description style="margin-bottom: 10px;word-break: break-all">{{url}}</home-description>
      <primary-button :disabled="!url" @click="copy()" style="width: 50%">{{ $t('share.copyUrl') }}</primary-button>
    </home-card>
  </div>
</template>

<script setup>
import {ref, watch} from 'vue'
import { useQRCode } from '@vueuse/integrations/useQRCode'
import HomeCard from "@/components/HomeCard";
import HomeTitle from "@/components/HomeTitle";
import PrimaryButton from "@/components/PrimaryButton";
import HomeDescription from "@/components/HomeDescription";
import { useClipboard } from '@vueuse/core'
import {successToast} from "@/util";
import i18n from "@/i18n-setup";
import contractInterface from "@/contractInterfaceWrap";

const url = ref('http://www.baidu.com?invitation=0x0000000000000000000000000000000000000000')
const qrcode = useQRCode(url, {
  errorCorrectionLevel: 'H',
  margin: 3,
})
const { copy, copied } = useClipboard({ source: url })
watch(copied, val => val && successToast(i18n.global.t('copySuccess')))
initShareQrCode(url)

async function initShareQrCode(url) {
  url.value = await contractInterface.fetchShareQrCode()
}
</script>
