<template>
  <div style="height: 20px"></div>
  <basic-list @refresh="onRefresh" @load="onLoad">
    <van-cell @click="onClick(item)" v-for="(item) in list" :key="item">
      <template #title>
        <van-row justify="space-between">
          <van-col style="padding-left: 20px">
            {{ $t('nftDetail.' + item.key) }}
          </van-col>
          <van-col>
<!--            {{ item.id }}-->
            <van-icon name="arrow"></van-icon>
          </van-col>
        </van-row>
      </template>
    </van-cell>
  </basic-list>
</template>
<script setup>
import BasicList from "@/components/BasicList";
import {ref} from "vue";
import dlv from 'dlv'
import router from "@/router";
import contractInterface from "@/contractInterfaceWrap";

const list = ref([])

function onLoad({loading, pageNum, pageSize, finished, error, totalNum }) {
  loading.value = true
  contractInterface.fetchNFTList({ pageNum: pageNum.value, pageSize: pageSize.value })
      .then(res => {
        if (res.code == 0) {
          success({finished, totalNum, pageNum, error, res})
        } else {
          handleListError({finished, loading, error})
        }
      })
      .catch(() => {
        handleListError({finished, loading, error})
      })
      .finally(() => {
        loading.value = false
      })
}


function onRefresh({loading, pageNum, pageSize, finished, error, refreshing, totalNum }) {
  refreshing.value = true
  pageNum.value = 1
  list.value = []
  contractInterface.fetchNFTList({ pageNum: pageNum.value, pageSize: pageSize.value })
      .then(res => {
        if (res.code == 0) {
          success({finished, totalNum, pageNum, error, res})
        } else {
          handleListError({finished, loading, error})
        }
      })
      .catch(() => {
        handleListError({finished, loading, error})
      })
      .finally(() => {
        loading.value = false
        refreshing.value = false
      })
}

function handleListError({finished, loading, error}) {
  error.value = true
  finished.value = false
  loading.value = false
}

function success({finished, totalNum, pageNum,error, res}) {
  finished.value = dlv(list, 'value.length') === res.data.totalNum
  totalNum.value = res.data.totalNum
  pageNum.value++

  if (dlv(res, 'data.list.length')) {
    list.value.push(...res.data.list)
  }

  error.value = false
}

function onClick(item) {
  router.push({
    path: '/NFTDetail',
    query: {
      key: item.key,
      id: item.id
    }
  })
}
</script>

