// IDO 倒计时获取处理，返回 时间戳 resolve(时间戳)
function fetchIDOTimestamp() {
    const countdownTimeStamp = Date.now() + 10 * 60 * 1000
    return Promise.resolve(countdownTimeStamp)
}

// 分享 返回分享的地址
async function fetchShareQrCode() {
    await awaitSeconds()
    return Promise.resolve('http://www.baidu.com?invitation=0x1000000000000000000000000000000000000001')
}

async function fetchIdoClick(item) {
    await awaitSeconds()
    return Promise.resolve(item)
}

// IDO页面(WS) 和 获得奖励(WS USDT NFT)
async function fetchIdoDatas() {
    return Promise.resolve({
        IDO_WS: 100, // IDO页面的 WS 数字
        IDO_WS__release: true, // IDO页面的 WS 提取状态 待释放为false, 已释放为true

        GetRewarded_WS: 1000, // 获得奖励页面的 WS 数字
        GetRewarded_WS_release: true, // 获得奖励页面的 WS 提取状态 待释放为false, 已释放为true
        GetRewarded_USDT: 10000,// 获得奖励页面的 USDT 数字
        GetRewarded_USDT_release: false,// 获得奖励页面的 USDT 提取状态 待释放为false, 已释放为true
        GetRewarded_NFT: 100000,// 获得奖励页面的 NFT 数字
        GetRewarded_NFT_release: false// // 获得奖励页面的 NFT 提取状态 待释放为false, 已释放为true
    })
}

