import contractInterface from "./contractInterface_";
import {handleError, loading, loadingClose} from "@/util";

let initFlag = false;
const newContractInterface = {};
Object.keys(contractInterface).forEach(key => {
    if(key == "init") {
        newContractInterface[key] = contractInterface[key];
        return;
    }
    newContractInterface[key] = async (...args) => {
        const loadingInstance = key !== "fetchIdoDatas"? loading():null;

        try {
            if (!initFlag) {
                await contractInterface.init();
                initFlag = true;
            }
            return await contractInterface[key](...args);
        } catch (e) {
            handleError(e)
        }finally {
            loadingInstance && loadingClose(loadingInstance);
        }

    }
});
export default newContractInterface;

