<template>
  <div style="height: 20px"></div>
  <basic-list @refresh="onRefresh" @load="onLoad">
    <van-cell v-for="(item, index) in list" :key="item">
      <template #title>
        <van-row justify="space-around" align="center">
          {{ index + 1 }}
          <van-col>
            <van-row justify="center">
              {{ prettyAddress(item.address) }}
              <van-button @click="copy(item.address)" type="primary" size="mini" style="margin-left: 10px">{{ $t('copy') }}</van-button>
            </van-row>
          </van-col>
<!--          <van-col>-->
<!--            {{ item.time }}-->
<!--          </van-col>-->
        </van-row>
      </template>
    </van-cell>
  </basic-list>
</template>
<script setup>
import BasicList from "@/components/BasicList";
import {ref, watch} from "vue";
import dlv from 'dlv'
import contractInterface from "@/contractInterfaceWrap";
import { useClipboard, usePermission } from '@vueuse/core'
import {successToast} from "@/util";
import i18n from "@/i18n-setup";


usePermission('clipboard-read')
usePermission('clipboard-write')

const list = ref([])

const { copy, copied } = useClipboard({ source: '' })
watch(copied, val => val && successToast(i18n.global.t('copySuccess')))

function onLoad({loading, pageNum, pageSize, finished, error, totalNum }) {
  loading.value = true
  contractInterface.fetchDirectPushList({ pageNum: pageNum.value, pageSize: pageSize.value })
      .then(res => {
        if (res.code == 0) {
          success({finished, totalNum, pageNum, error, res})
        } else {
          handleListError({finished, loading, error})
        }
      })
      .catch(() => {
        handleListError({finished, loading, error})
      })
      .finally(() => {
        loading.value = false
      })
}


function onRefresh({loading, pageNum, pageSize, finished, error, refreshing, totalNum }) {
  refreshing.value = true
  pageNum.value = 1
  list.value = []
  contractInterface.fetchDirectPushList({ pageNum: pageNum.value, pageSize: pageSize.value })
      .then(res => {
        if (res.code == 0) {
          success({finished, totalNum, pageNum, error, res})
        } else {
          handleListError({finished, loading, error})
        }
      })
      .catch(() => {
        handleListError({finished, loading, error})
      })
      .finally(() => {
        loading.value = false
        refreshing.value = false
      })
}

function handleListError({finished, loading, error}) {
  error.value = true
  finished.value = false
  loading.value = false
}

function success({finished, totalNum, pageNum,error, res}) {
  finished.value = dlv(list, 'value.length') === res.data.totalNum
  totalNum.value = res.data.totalNum
  pageNum.value++

  if (dlv(res, 'data.list.length')) {
    list.value.push(...res.data.list)
  }

  error.value = false
}


function prettyAddress(address, centerPlaceholder = '****') {
  return address.slice(0, 8) + centerPlaceholder + address.slice(0, 4)
}
</script>

