import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home.vue'
import IDO from '../views/IDO.vue'
import Share from '../views/Share.vue'
import DirectPushList from '../views/DirectPushList.vue'
import GetRewarded from '../views/GetRewarded.vue'
import NFTList from '../views/NFTList.vue'
import NFTDetail from '../views/NFTDetail.vue'
import {invitationSetup} from "@/custom/invite";

export const menu = [
  {
    path: '/',
    name: 'Home',
    meta: {
      // isBack: true
    },
    component: Home
  },{
    path: '/IDO',
    name: 'IDO',
    meta: {
    },
    component: IDO
  },{
    path: '/DirectPushList',
    name: 'DirectPushList',
    meta: {
    },
    component: DirectPushList
  },{
    path: '/GetRewarded',
    name: 'GetRewarded',
    meta: {
    },
    component: GetRewarded
  },{
    path: '/Share',
    name: 'Share',
    meta: {
    },
    component: Share
  }
]
const others = [
  {
    path: '/NFTList',
    name: 'NFTList',
    meta: {
      isBack: true
    },
    component: NFTList
  },{
    path: '/NFTDetail',
    name: 'NFTDetail',
    meta: {
      isBack: true
    },
    component: NFTDetail
  }
]
const routes = [
  ...menu,
  ...others,
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.afterEach((to, from, next) => {
  invitationSetup({to, from, next})
})

export default router
