import {ref} from "vue";
import contractInterface from "@/contractInterfaceWrap";
import {setIntervalGlobal, getMilliseconds} from "@/util";

export const state = ref({
    IDODatas: {
        IDO_WS: 0, // IDO页面的 WS 数字
        IDO_WS__release: false, // IDO页面的 WS 提取状态 待释放为false, 已释放为true
        IDO_EndTime: 0,
        IDO_isOver: true,
        GetRewarded_WS: 0, // 获得奖励页面的 WS 数字
        GetRewarded_WS_release: false, // 获得奖励页面的 WS 提取状态 待释放为false, 已释放为true
        GetRewarded_USDT: 0,// 获得奖励页面的 USDT 数字
        GetRewarded_USDT_release: false,// 获得奖励页面的 USDT 提取状态 待释放为false, 已释放为true
        GetRewarded_NFT: 0,// 获得奖励页面的 NFT 数字
        GetRewarded_NFT_release: false// // 获得奖励页面的 NFT 提取状态 待释放为false, 已释放为true
    },
})

export async function getData() {
    const fetchIdoDatas_model = await contractInterface.fetchIdoDatas();
    state.value.IDODatas.IDO_EndTime = getMilliseconds(fetchIdoDatas_model.IDO_EndTime)
    state.value.IDODatas.IDO_isOver = fetchIdoDatas_model.IDO_isOver
    state.value.IDODatas.IDO_WS = fetchIdoDatas_model.IDO_WS
    state.value.IDODatas.IDO_WS__release = fetchIdoDatas_model.IDO_WS__release
    state.value.IDODatas.GetRewarded_WS = fetchIdoDatas_model.GetRewarded_WS
    state.value.IDODatas.GetRewarded_WS_release = fetchIdoDatas_model.GetRewarded_WS_release
    state.value.IDODatas.GetRewarded_USDT = fetchIdoDatas_model.GetRewarded_USDT
    state.value.IDODatas.GetRewarded_USDT_release = fetchIdoDatas_model.GetRewarded_USDT_release
    state.value.IDODatas.GetRewarded_NFT = fetchIdoDatas_model.GetRewarded_NFT
    state.value.IDODatas.GetRewarded_NFT_release = fetchIdoDatas_model.GetRewarded_NFT_release
}

setIntervalGlobal({
    fun: getData
})
