<template>
  <van-action-sheet
      v-model:show="show_"
      :actions="actions"
      @select="onSelect"
      :cancel-text="$t('cancel')"
      @cancel="onCancel"
  />
</template>

<script setup>
import { defineProps, defineEmits, computed} from "vue";
import {setI18nLocale} from '@/i18n-setup'
import {publicConfig} from "@/lang/public";

const emit = defineEmits(['update:show'])
const props = defineProps({
  show: {
    type: Boolean,
    default: false
  }
})

const actions = (publicConfig.languages)

function onSelect(item) {
  if (item) {
    setI18nLocale(item.value)
    localStorage.setItem('lang', item.value)
  }
  show_.value = false
}

function onCancel() {
  show_.value = false
}

const show_ = computed({
  get() {
    return props.show
  },
  set(val) {
    emit('update:show', val)
  }
})

</script>
