// https://vue-i18n.intlify.dev/installation.html
import {createI18n} from 'vue-i18n'
import en from './lang/en.json'
import zh from './lang/zh.json'
import ru from './lang/ru.json'
import ja from './lang/ja.json'
import ko from './lang/ko.json'

import { Locale } from 'vant';
// 引入英文语言包
import enUS from 'vant/es/locale/lang/en-US';
import zhCN from 'vant/es/locale/lang/zh-CN';
import koKR from 'vant/es/locale/lang/ko-KR';
import jaJP from 'vant/es/locale/lang/ja-JP';
import ruRU from 'vant/es/locale/lang/ru-RU';
import {deepSerialization} from "@/util";

console.log(zhCN)
Locale.add({
    en: enUS,
    zh: zhCN,
    ru: ruRU,
    ja: jaJP,
    ko: koKR
});
const messages = {
    en,
    zh,
    ru,
    ja,
    ko
}
export const i18n = createI18n({
    locale: getLocal(), // set locale
    fallbackLocale: 'en', // 访问出错时使用的语言
    messages: (() => {
        return Object.keys(messages)
            .reduce((total,key) => {
                total[key] = deepSerialization(messages[key])
                return total
            }, {})
    })() // set locale messages
})

export function setI18nLocale(value) {
    setTimeout(() => {
        i18n.global.locale = value
    })
    Locale.use(value);
}

export default i18n

/**
 * zh,cn
 * @returns {string}
 */
function getLocal() {
    const lang = localStorage.getItem('lang')
    if (lang) {
        setI18nLocale(lang)
        return lang
    }
    if (navigator.language.startsWith('zh')) {
        setI18nLocale('zh')
        return 'zh'
    }
    if (navigator.language.startsWith('ko')) {
        setI18nLocale('ko')
        return 'ko'
    }
    if (navigator.language.startsWith('ko')) {
        setI18nLocale('ko')
        return 'ko'
    }
    if (navigator.language.startsWith('ru')) {
        setI18nLocale('ru')
        return 'ru'
    }
    if (navigator.language.startsWith('ja')) {
        setI18nLocale('ja')
        return 'ja'
    }
    setI18nLocale('en')
    return 'en'
}
