<template>
  <van-pull-refresh v-model="refreshing" @refresh="onRefresh" style="min-height: 100vh;">
    <van-list
        class="invite-detail__list"
        v-model:loading="loading"
        v-model:error="error"
        :loading-text="$t('loading')"
        :finished="finished"
        :finished-text="$t('noMore')"
        :error="error"
        :error-text="$t('error')"
        @load="onLoad"
    >
      <slot></slot>
    </van-list>
  </van-pull-refresh>
</template>

<script setup>
import {ref,defineEmits} from "vue";

const emits = defineEmits(['load', 'refresh'])

const loading = ref(false)
const finished = ref(false)
const error = ref(false)
const refreshing = ref(false)
const pageSize = ref(20)
let pageNum = ref(1)
let totalNum = ref(0)

function onLoad() {
  emits('load', {
    loading,
    refreshing,
    error,
    finished,
    pageSize,
    pageNum,
    totalNum
  })
}

function onRefresh() {
  emits('refresh',{
    loading,
    refreshing,
    error,
    finished,
    pageSize,
    pageNum,
    totalNum
  })
}

</script>
