<template>
  <img class="home__img" src="../assets/index.jpg" alt=""/>
  <van-row justify="center">
    <van-col span="auto">
      <div class="ido__countdown-wrap">
        <home-title>{{ $t('ido.countdown') }}</home-title>
        <basic-countdown class="mt-12" :time="state.IDODatas.IDO_EndTime"></basic-countdown>
      </div>
    </van-col>
  </van-row>

  <van-row justify="center" class="ido__shopping-description mt-35">{{ $t('ido.onlyOneForAddress') }}</van-row>

  <div style="margin: 12px">
    <template v-for="(item, index) in data" :key="item.USDT">
      <div :class="{'mt-35': index !== 0}" >
        <van-row justify="center">
          <custom-i-d-o-big-button :disabled="state.IDODatas.IDO_isOver" @click="onClick({...item}, index)">
            <template #left>
              {{ item.USDT }}USDT
            </template>
            <template #right>
              {{ item.WS }}WS
            </template>
          </custom-i-d-o-big-button>
        </van-row>
      </div>
    </template>
  </div>

  <home-card class="mt-40 mlr-12">
    <van-row class="card-title">WS</van-row>
    <basic-input>
      <van-field disabled label="" v-model="state.IDODatas.IDO_WS"></van-field>
    </basic-input>
    <div class="mt-12"></div>
    <primary-button @click="onClickWS({value: state.IDODatas.IDO_WS, type: 'WS'})" :disabled="!state.IDODatas.IDO_WS__release" width="120px" type="success" round>{{ state.IDODatas.IDO_WS__release?$t('ido.extract'):$t('ido.unreleased') }}</primary-button>
  </home-card>
</template>

<script setup>

import HomeTitle from "@/components/HomeTitle";
import {ref} from "vue";
import {successToast} from "@/util";
import contractInterface from "@/contractInterfaceWrap";
import BasicCountdown from "@/components/BasicCountdown";
import CustomIDOBigButton from "@/components/CustomIDOBigButton";
import HomeCard from "@/components/HomeCard";
import BasicInput from "@/components/BasicInput";
import PrimaryButton from "@/components/PrimaryButton";
import {state} from "@/state";
import i18n from "@/i18n-setup";
import {publicConfig} from "@/lang/public";

const data = ref(publicConfig.IDOTransferRules)

async function onClick(item) {
    const result = await contractInterface.fetchIdoClick(item);
    if (result) successToast(i18n.global.t('successfully'));
}

async function onClickWS({value, type}) {
    const result = await contractInterface.fetchIDO_WS({value, type})
    if (result) successToast(i18n.global.t('successfully'));
}

</script>

<style>
.ido__countdown-wrap {
  box-shadow: 1px 1px 6px #000;
  margin-top: -47px;
  padding: 20px;
  background-color: var(--van-primary-color);
  border-radius: 5px;
}

.ido__shopping-description {
  color: var(--van-gray-6);
}
</style>
