<template>
  <van-button class="home__shadow" :type="type" size="large" :disabled="disabled">
    <slot></slot>
  </van-button>
</template>

<script setup>
import {defineProps, getCurrentInstance, onMounted} from "vue";

const props = defineProps({
  big: {
    type: Boolean,
    default: false
  },
  round: {
    type: Boolean,
    default: false
  },
  width: {
    type: String,
    default: '100%'
  },
  type: {
    type: String,
    default: 'primary'
  },
  disabled: {
    type: Boolean,
    default: false
  }
})

onMounted(() => {
  const vm = getCurrentInstance().proxy
  if (props.big) {
    vm.$el.querySelector('.van-button__text').setAttribute('style', 'display:flex;flex:1;height: 100%;')
    vm.$el.setAttribute('style', 'line-height:unset;height:unset')
  }
  if (props.round) {
    const style = vm.$el.getAttribute('style')
    vm.$el.setAttribute('style', style + ';border-radius: 20px')
  }
  if (props.width) {
    vm.$el.style.width = 'unset'
    vm.$el.style.minWidth = props.width
    vm.$el.style.maxWidth = '80%'
  }
  vm.$el.style.boxShadow = '0 .5em 1em ' + `var(--van-button-${props.type}-background-color)`
})

</script>

<style>
.home__shadow {
  box-shadow: 0 .5em 1em rgba(0,129,255,.9);
}
</style>
