<template>
  <div class="get-rewarded">
    <home-card v-for="type in types" :key="type" class="mt-40 mlr-12">
      <van-row class="card-title">{{ type }}</van-row>
      <basic-input>
        <van-field disabled label="" v-model="state.IDODatas['GetRewarded_' + type]"></van-field>
        <router-link to="/NFTList">
          <van-button v-if="hasDetail(type)" type="primary" size="mini" style="right: 10px;top:10px;position: absolute">
            {{ $t('ido.detail') }}
          </van-button>
        </router-link>
      </basic-input>
      <div class="mt-12"></div>
      <primary-button @click="onClick({type, value: state.IDODatas['GetRewarded_' + type]})" :disabled="!state.IDODatas['GetRewarded_' + type + '_release']" width="120px" type="success" round>{{ state.IDODatas['GetRewarded_' + type + '_release']?$t('ido.extract'):$t('ido.unreleased') }}</primary-button>
    </home-card>
  </div>
</template>

<script setup>
import HomeCard from "@/components/HomeCard";
import BasicInput from "@/components/BasicInput";
import PrimaryButton from "@/components/PrimaryButton";
import {ref} from "vue";
import {state} from "@/state";
import contractInterface from '@/contractInterfaceWrap'
import i18n from "@/i18n-setup";
import {successToast} from "@/util";

const types = ref(['WS', 'USDT', 'NFT'])
const hasDetail = type => type == 'NFT'

async function onClick({type, value}) {
  const result = await contractInterface['fetchGetRewarded_' + type]({type, value})
  if (result) {
    successToast(i18n.global.t('successfully'))
    state.value.IDODatas['GetRewarded_' + type] = 0
  }
}
</script>
