// Toast
import {Dialog, Toast} from 'vant';
import { Notify } from 'vant';
import 'vant/es/toast/style';
import 'vant/es/notify/style';
import i18n from './i18n-setup'

Toast.allowMultiple(true);

const loadingStatus = {
    index: 0,
    tempClosed: false,
    list: {}
};

// 成功的提示
export function successToast(message) {
    Toast.success(message);
}

// 失败的提示
export function errorToast(message) {
    Toast.fail(message);
}

// 成功的消息通知
export function successNotify(message) {
    Notify({
        type: 'success',
        message
    });
}
// 警告的消息通知
export function warningNotify(message) {
    Notify({
        type: 'warning',
        message
    });
}
// 失败的消息通知
export function errorNotify(message) {
    Notify({
        type: 'danger',
        message
    });
}

// 显示确定按钮的弹框提示
export async function showAlert(options) {
    tempCloseAllLoading();
    try {
        return await Dialog.alert({title: i18n.global.t('tips'), ...options})
    }finally {
        tempOpenAllLoading();
    }
}

// 显示确定取消按钮的弹框提示
export async function showConfirm(options) {
    tempCloseAllLoading();
    try {
        await Dialog.confirm({title: i18n.global.t('tips'), ...options})
        return true;
    }catch (err){
        return false;
    }finally {
        tempOpenAllLoading();
    }
}

// 全屏加载中
export function loading(options = {
    message: i18n.global.t("loading"),
    forbidClick: true,
    duration: 0
}) {
    console.log(loadingStatus);
    loadingStatus.list[++loadingStatus.index] = {
        options,
        instance: loadingStatus.tempClosed ? null: Toast.loading(options)
    };
    return loadingStatus.index;
}

// 关闭全屏加载中
export function loadingClose(loadingIndex) {
    const value = loadingStatus.list[loadingIndex];
    if(value) delete loadingStatus.list[loadingIndex];
    return value.instance && value.instance.clear();
}

export function tempCloseAllLoading(){
    console.log("tempCloseAllLoading");
    if(loadingStatus.tempClosed) return;
    loadingStatus.tempClosed = true;
    for(const value of Object.values(loadingStatus.list)){
        value.instance.clear();
        value.instance = null;
    }
}

export function tempOpenAllLoading(){
    console.log("tempOpenAllLoading");
    if(!loadingStatus.tempClosed) return;
    for(const value of Object.values(loadingStatus.list)){
        value.instance = Toast.loading(value.options);

        // value.instance.clear();
        // value.instance = null;
    }

    loadingStatus.tempClosed = false;
}




// 国际化辅助函数
export function atobWrap(value) {
    return window.atob(value)
}

// 获取到计时，输入时间戳
export function getMilliseconds(timestamp) {
    return timestamp - Date.now()
}

// 序列化字符串
export function serialization(obj) {
    return encodeURIComponent(JSON.stringify(obj))
}

// 反序列化字符串
export function serializationReverse(str) {
    return JSON.parse(decodeURIComponent(str))
}

export function fetchData({pageSize, pageNum, obj = {
    address: '0x183423423482s8df8s7df8s7df8s7d8fd8e',
    time: new Date().toLocaleTimeString()
}}) {
    if (pageNum == 1) {
        fetchData.count = 0
        fetchData.sendedNum = 0
    }
    fetchData.count = fetchData.count || 0
    fetchData.count = fetchData.count + 1
    fetchData.sendedNum = fetchData.sendedNum || 0
    fetchData.totalNum = 100
    return new Promise((resolve, reject) => {
        let mockList = Array.from({length: pageSize}).fill(obj)
        setTimeout(() => {
            if (fetchData.count == 3) {
                resolve({
                    code: -1,
                    msg: 'error'
                })
                return
            }
            if (fetchData.count > 4) {
                reject({})
                return
            }
            if (fetchData.sendedNum === fetchData.totalNum) {
                mockList = []
            }
            fetchData.sendedNum = fetchData.sendedNum + mockList.length
            resolve({
                code: 0,
                data: {
                    list: mockList,
                    totalNum: fetchData.totalNum,
                    pageNum: pageNum,
                }
            })
        }, 3e2)
    })
}

// 是否是正确的地址 地址正则：/^0x[0-9a-fA-F]{40}$/ 共42位
export function isAddress(address) {
    return /^0x[0-9a-fA-F]{40}$/.test(address)
}

export function deepSerialization(obj) {
    let type
    if (!['[object Object]'].includes(Object.prototype.toString.apply(obj))) {
        return
    }
    for (const key in obj) {
        type = Object.prototype.toString.apply(obj[key])
        if(['[object Array]'].includes(type)) {
            obj[key] = serialization(obj[key])
        } else if(['[object Object]'].includes(type)){
            deepSerialization(obj[key])
        }
    }
    return obj
}

// 定时刷新数据
const setIntervalGlobalFuns = []
export function setIntervalGlobal({fun, args, time = 1500}) {
    setIntervalGlobalFuns.push({
        fun,
        clearId: setInterval(() => {
            fun(args)
        }, time),
        args
    })
}

// 统一错误处理
export function handleError(e) {
    errorToast(e.message)
}
