<template>
  <van-icon :size="size" :name="name" ></van-icon>
</template>

<script setup>
import {defineProps, getCurrentInstance, onMounted} from "vue";

const props = defineProps({
  size: {
    type: [Number, String],
    default: 30
  },
  name: {
    type: String,
    default: '/arrow.svg'
  },
  noHeight: {
    type: Boolean,
    default: false
  }
})

onMounted(() => {
  if (props.noHeight) {
    const vm = getCurrentInstance().proxy
    vm.$el.querySelector('img').style.height = 'unset'
  }
})
</script>
